body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}div{border:1px solid}

@-webkit-keyframes moveing{0%{left:0px;top:0px}25%{left:50%;top:0px}50%{left:50%;top:50%}75%{left:0px;top:50%}100%{left:0px;top:0px}}@keyframes moveing{0%{left:0px;top:0px}25%{left:50%;top:0px}50%{left:50%;top:50%}75%{left:0px;top:50%}100%{left:0px;top:0px}}@-webkit-keyframes hiding{0%{opacity:0%}25%{opacity:50%}50%{opacity:100%}75%{opacity:50%}100%{opacity:0%}}@keyframes hiding{0%{opacity:0%}25%{opacity:50%}50%{opacity:100%}75%{opacity:50%}100%{opacity:0%}}.bonobono{opacity:0.3;z-index:-1;position:absolute;-webkit-animation-name:hiding;animation-name:hiding;-webkit-animation-duration:10s;animation-duration:10s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}

.rsp div{padding:15px;text-align:center}

