@keyframes moveing {
    0%   { left:0px; top:0px;}
    25%  { left:50%; top:0px;}
    50%  { left:50%; top:50%;}
    75%  { left:0px; top:50%;}
    100% { left:0px; top:0px;}
  }

@keyframes hiding{
  0% { opacity: 0%;}
  25% { opacity: 50%;}
  50% { opacity: 100%;}
  75% { opacity: 50%;}
  100% {opacity: 0%;}
}

.bonobono {
    opacity: 0.3;
    z-index: -1;
    position: absolute;
    animation-name: hiding;
    animation-duration: 10s;
    animation-iteration-count: infinite;
}